<template>
  <div class="comps-page">
    <div class="render-preview">
      <template v-if="renderCompList.length">
        <div class="preview-item" v-for="gItem in renderCompList" :key="gItem.gCode">
          <div class="preview-content">
            <comp-title :selectGroupComp="gItem" :selectComp="getSelectComp(gItem)"></comp-title>
            <div class="preview-body">
              <img class="preview-img" :src="imgMaps[gItem.selected]">
            </div>
          </div>
        </div>
      </template>
      <template v-else>
        <div class="no-comp">暂无数据</div>
      </template>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import CompTitle from './CompTitle'

export default {
  components: {
    CompTitle
  },
  data () {
    return {
      imgMaps: {
        FilterComplexInput_Enterprise: require('../assets/image/plugin/FilterComplexInput.gif'),
        FilterComplexInput_Personal: require('../assets/image/plugin/FilterComplexInput.gif'),
        FilterCombination_Enterprise: require('../assets/image/plugin/FilterCombination.gif'),
        FilterCombination_Personal: require('../assets/image/plugin/FilterCombination.gif')
      }
    }
  },
  computed: {
    ...mapState([
      'loginToken',
      'loginInfo',
      'appList',
      'appCompList'
    ]),
    renderCompList () {
      return this.appCompList.filter(item => item.gType === 'filters' && item.options.length > 0)
    }
  },
  destroyed () {
    clearTimeout(this.downTime)
  },
  methods: {
    getSelectComp (gItem) {
      if (gItem) {
        return gItem.options.find(item => item.code === gItem.selected)
      }
      return null
    },
    getCompPrivilege (gItem) {
      return this.loginToken && this.loginInfo && this.loginInfo.compList && this.loginInfo.compList.find(comp => gItem.options.some(item => item.code === comp.appCode))
    }
  }
}
</script>
